.service {
  border-radius: 20px;
}
.service img {
  width: 100%;
}
.image {
  border: 2px solid #fff;
  width: 100%;
  height: 184px;
  overflow: hidden;
}

.image img {
  width: 100%;
  height: 300;
  transition: all 2s ease-in-out;
}

.image:hover img {
  transform: scale(2, 2);
  cursor: pointer;
}
